.film {
  width: 21%;
  height: 100%;
  overflow: hidden;
  position: relative;
  padding: 40px;
  top: -25px;


  color: #999;
  font-size: 27px;
  line-height: 1.8em;
  letter-spacing: .09em;
  text-align: center;

  -webkit-transform-origin: 50% 100%;

      -ms-transform-origin: 50% 100%;

          transform-origin: 50% 100%;
  -webkit-animation: scroll 500s linear;
          animation: scroll 500s linear;
}

@-webkit-keyframes scroll {
	0% {
    top: -100px;
    -webkit-transform: rotateX(20deg)  translateZ(0);
            transform: rotateX(20deg)  translateZ(0);
  }
	100% {
    top: -6000px;
    -webkit-transform: rotateX(25deg) translateZ(-1500px);
            transform: rotateX(25deg) translateZ(-1500px);
  }
}

@keyframes scroll {
	0% {
    top: -100px;
    -webkit-transform: rotateX(20deg)  translateZ(0);
            transform: rotateX(20deg)  translateZ(0);
  }
	100% {
    top: -6000px;
    -webkit-transform: rotateX(25deg) translateZ(-1500px);
            transform: rotateX(25deg) translateZ(-1500px);
  }
}

.scrolling-text-container {
  -webkit-transform: rotateX(30deg);
          transform: rotateX(30deg);
}


@media screen and (max-width: 990px) {

  .film {
    padding: 10px;
  }
}

@media screen and (max-width: 935px) {

  .film {
    display: none;
  }
}



